import SuccessPopup from '@/components/Popups/SuccessPopup';
import { SecondaryButton } from '@/components/buttons/SecondaryButton';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import CancelPopup from '@/components/Popups/CancelPopup';
import { MaintenanceOrder } from '@/types/maintenance';
import { useNavigate } from 'react-router-dom';

interface Props {
    maintenanceOrder?: MaintenanceOrder | null;
    showSuccessPopup: boolean;
    setShowSuccessPopup: (value: boolean) => void;
    showCancelPopup: boolean;
    setShowCancelPopup: (value: boolean) => void;
    loading: boolean;
}
export default function Submission({
    maintenanceOrder,
    setShowCancelPopup,
    setShowSuccessPopup,
    showCancelPopup,
    showSuccessPopup,
    loading,
}: Props) {
    const navigate = useNavigate();

    const handleAddAnother = () => {
        setShowSuccessPopup(false);
    };
    return (
        <>
            <div className="flex flex-row justify-end items-center mt-8">
                {maintenanceOrder ? (
                    <SecondaryButton
                        type="button"
                        className="xl:px-[87px] xl:py-3 text-[#A21515] border border-red-error-50 xl:font-semibold bg-red-error-50"
                        onClick={() => setShowCancelPopup(true)}
                    >
                        إلغاء الشكوى
                    </SecondaryButton>
                ) : (
                    <SecondaryButton
                        type="button"
                        className="xl:px-[87px] xl:py-3 border bordr-[#E8EFF6] xl:font-semibold"
                        onClick={() => setShowCancelPopup(true)}
                    >
                        الغاء
                    </SecondaryButton>
                )}
                <div>
                    {maintenanceOrder ? (
                        <PrimaryButton className="xl:px-[91px] xl:py-3 xl:font-semibold ms-6" disabled={loading}>
                            {loading ? 'جاري التحميل...' : 'حفظ التغييرات'}
                        </PrimaryButton>
                    ) : (
                        <PrimaryButton className="xl:px-[91px] xl:py-3 xl:font-semibold ms-6" disabled={loading}>
                            {loading ? 'جاري التحميل...' : 'تسجيل الشكوى'}
                        </PrimaryButton>
                    )}
                </div>
            </div>
            <CancelPopup
                open={showCancelPopup}
                onClickButton1={() => {
                    setShowCancelPopup(false);
                }}
                onClickButton2={() => {
                    navigate('/customer-relations/complaints');
                    setShowCancelPopup(false);
                }}
                text1={maintenanceOrder ? 'إلغاء تعديل بيانات تسجيل الشكوى' : 'إلغاء تسجيل الشكوى'}
                text2={
                    maintenanceOrder
                        ? 'أنت على وشك إلغاء عملية تعديل بيانات الشكوى دون إكمال البيانات المطلوبة. سيتم تجاهل جميع البيانات المدخلة، هل أنت متأكد؟'
                        : 'أنت على وشك إلغاء عملية إضافة الشكوى دون إكمال البيانات المطلوبة. سيتم تجاهل جميع البيانات المدخلة، هل أنت متأكد؟'
                }
                textButton1="إكمال الشكوى"
                textButton2="إلغاء الشكوى"
            />
            <SuccessPopup
                text1={maintenanceOrder ? 'تم تعديل بيانات الشكوى بنحاح' : 'تم إضافة الشكوى بنحاح'}
                text2={
                    maintenanceOrder
                        ? 'تم تعديل بيانات الشكوى المطلوبة، يمكنك المتابعه مع مركز الشكاوى".'
                        : 'تم تعديل بيانات الشكوى المطلوبة، يمكنك المتابعه مع مركز الشكاوى".'
                }
                textButton1="العودة لقائمة الشكاوى"
                textButton2={maintenanceOrder ? 'تعديل بيانات اخرى' : 'إضافة شكوى اخرى'}
                open={showSuccessPopup}
                onAddAnother={handleAddAnother}
                onReturnToList={() => {
                    navigate('/customer-relations/complaints');
                    setShowSuccessPopup(false);
                }}
            />
        </>
    );
}
