import SearchableSelectInputForm from '@/components/forms/SearchableSelectInputForm';
import SelectInputForm from '@/components/forms/SelectInputForm';
import TextInputForm from '@/components/forms/TextInputForm';
import { endpoints } from '@/lib/axios/axios-global';
import { apiGet } from '@/lib/axios/axios-utils';
import { Customer } from '@/types/customer.types';
import { MaintenanceOrder } from '@/types/maintenance';
import { useEffect, useState } from 'react';
import { useFormContext } from 'react-hook-form';

interface Props {
    maintenanceOrder?: MaintenanceOrder | null;
}
export default function ComplaintInfo({ maintenanceOrder }: Props) {
    const { setValue } = useFormContext();
    const [customers, setCustomers] = useState<{ name: string; value: string }[]>([]);
    const [customersError, setCustomersError] = useState<string | null>(null);

    useEffect(() => {
        const fetchCustomers = async () => {
            try {
                const response = await apiGet({ endpoint: endpoints.customer.list });

                const options = response.data.data.data.map((customer: Customer) => {
                    return { name: customer.full_name, value: String(customer.id) };
                });
                setCustomers(options);
                setCustomersError(null);
            } catch (error) {
                setCustomersError('Error fetching customers. Please try again later.');
            }
        };

        fetchCustomers();
    }, []);

    useEffect(() => {
        if (customers.length > 0) {
            setValue(
                'maintenance_center_id',
                maintenanceOrder?.maintenance_center?.id ? String(maintenanceOrder?.maintenance_center?.id) : undefined,
            );
        }
    }, [customers]);

    return (
        <div className="my-6">
            <div className="grid grid-cols-1 gap-6">
                <SearchableSelectInputForm
                    name="customer_id"
                    placeholder="بحث عن اسم أو كود العميل"
                    label="اسم العميل"
                    options={customers}
                    error={customersError}
                    // data={maintenanceOrder}
                />
                <TextInputForm name="title" placeholder="بحث عن منتج، أو كتابة عنوان الشكوى" label="موضوع الشكوى" />
                <SelectInputForm
                    name="receiving_method"
                    placeholder="خيارات"
                    label="طريقة استقبال الشكوى"
                    options={[
                        { name: 'البريد الإلكتروني', value: 'email' },
                        { name: 'الهاتف', value: 'phone' },
                        { name: 'الفاكس', value: 'fax' },
                        { name: 'البريد العادي', value: 'mail' },
                    ]}
                    error={customersError}
                    data={maintenanceOrder}
                />
                <SelectInputForm
                    name="department"
                    placeholder="خيارات"
                    label="القسم المختص"
                    options={[
                        { name: 'القسم الأول', value: '1' },
                        { name: 'القسم الثاني', value: '2' },
                        { name: 'القسم الثالث', value: '3' },
                        { name: 'القسم الرابع', value: '4' },
                    ]}
                    error={customersError}
                    data={maintenanceOrder}
                />
            </div>
        </div>
    );
}
