import { Form } from '@/components/ui/form';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { apiPost } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { useMemo, useState } from 'react';
import { RichTextEditor } from '@/components/forms/textEditor/RichTextInput';
import { MaintenanceOrder } from '@/types/maintenance';
import ComplaintInfo from './ComplaintInfo';
import { AddComplaintFormValues, AddComplaintSchema } from '../schemas/add-complaint-schema';
import Submission from './Submission';

interface Props {
    maintenanceOrder?: MaintenanceOrder | null;
}

export default function AddComplaintForm({ maintenanceOrder }: Props) {
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);
    const [showSuccessPopup, setShowSuccessPopup] = useState(false);
    const [showCancelPopup, setShowCancelPopup] = useState(false);

    const defaultValues = useMemo(
        () => ({
            customer_id: maintenanceOrder?.description || '',
            title: maintenanceOrder?.cost ? String(maintenanceOrder?.cost) : undefined,
            receiving_method: maintenanceOrder?.description || '',
            department: maintenanceOrder?.description || '',
            description: maintenanceOrder?.description || '',
        }),
        [maintenanceOrder],
    );

    const form = useForm<AddComplaintFormValues>({
        resolver: zodResolver(AddComplaintSchema),
        defaultValues,
    });

    const onSubmit = async (data: AddComplaintFormValues) => {
        setLoading(true);
        setError(null); // Clear previous errors
        try {
            await apiPost({
                endpoint: maintenanceOrder
                    ? endpoints.complaint.update + maintenanceOrder.id
                    : endpoints.complaint.create,
                body: data,
                // config: { headers: { 'Content-Type': 'multipart/form-data' } },
            });
            setShowSuccessPopup(true);
        } catch (error: any) {
            if (error.response?.data?.errors) {
                // Handle field-specific errors
                Object.entries(error.response.data.errors).forEach(([field, messages]) => {
                    form.setError(field as any, {
                        type: 'manual',
                        message: (messages as string[]).join(', '),
                    });
                });
            } else {
                // Handle generic error
                let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';
                if (error.response) {
                    errorMessage = error.response.data.msg || errorMessage;
                } else if (error.request) {
                    errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
                }
                setError(errorMessage);
            }
        } finally {
            setLoading(false);
        }
    };

    return (
        <>
            <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="mb-8">
                    {error && (
                        <div className="mb-4 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg">
                            <div className="flex justify-between items-center">
                                <span className="">{error}</span>
                                <button onClick={() => setError(null)} className="text-red-700 hover:text-red-900">
                                    ×
                                </button>
                            </div>
                        </div>
                    )}
                    <ComplaintInfo maintenanceOrder={maintenanceOrder} />
                    <div>
                        <p className="text-primery-blue-900 font-medium text-[18px] leading-[33.73px] tracking-normal text-start mb-4">
                            نص الشكوى
                        </p>
                        <RichTextEditor
                            form={form}
                            data={maintenanceOrder?.description}
                            name="description"
                            placeholder="ادخل وصف المنتج"
                        />
                    </div>
                    <Submission
                        maintenanceOrder={maintenanceOrder}
                        loading={loading}
                        setShowCancelPopup={setShowCancelPopup}
                        setShowSuccessPopup={setShowSuccessPopup}
                        showCancelPopup={showCancelPopup}
                        showSuccessPopup={showSuccessPopup}
                    />
                </form>
            </Form>
        </>
    );
}
