import { z } from 'zod';

export const AddComplaintSchema = z.object({
    customer_id: z.string().min(1, 'Customer field is required.'),
    title: z.string().min(1, 'Address is required'),
    receiving_method: z.string().min(1, 'The complaint reception type field is required.'),
    department: z.string().min(1, 'Department is required'),
    description: z.string().min(1, 'Description is required'),
});

export type AddComplaintFormValues = z.infer<typeof AddComplaintSchema>;
